<template>
  <v-container>
    <evProponente :proponente="proponente" :evaluacion="true" v-if="proponente"/>
    <v-dialog
      v-model="loading"
      persistent
      width="300"
      >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          {{textDialog}}
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import evProponente from '@/components/solProcContrata/evProponente'
import Proponente from '../../servicios/Proponente'
export default {
  components: { evProponente },
  data () {
    return {
      proponente: '',
      loading: false,
      textDialog: 'Cargando...',
    }
  },
  async mounted() {
    const idProp = this.$route.params.idProp
    const proponente = this.$route.query.proponente
    try {
      this.loading = true
      const { data } = await Proponente.getCriterios(idProp)
      this.proponente = data
    } catch (error) {
      console.log(err)
    }
    this.loading = false
  },
}
</script>
